.button{
  text-transform: uppercase;
  color: #fff;
  padding: 17px;
  border: 1px solid #fff;
  border-radius: 3px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 1.3px;
  display: inline-flex;
  align-items: center;
  transition: 200ms ease-in-out;
  &--bg-blue{
    &:hover{
      background-color: $sea;
      border: 1px solid $sea;
    }
  }
  &:hover{
    .arrow{
      transform: rotate(90deg);
    }
  }
  .arrow{
    margin-left: 10px;
    display: block;
    transition: all 180ms ease-in-out;
  }
}