.about{
  padding: 100px 0 140px;
  &__contents{
    display: flex;
    align-items: center;
  }
  
  &__history{
    text-align: center;
    width: calc((100% / 2) - 40px);
    margin-right: 40px;
  }
  &__img{
    width: 280px;
    height: 260px;
    clip-path: polygon(22% 0, 78% 0, 100% 50%, 82% 100%, 21% 100%, 0% 50%);
    margin-bottom: 30px;
  }
  &__who{
    color: $grey;
    font-size: 34px;
    font-weight: 700;
    margin-bottom: 15px;
  }
  &__text{
    font-size: 16px;
    line-height: 1.6;
  }
  &__anchor{
    color: $sea;
    font-weight: 500;
    &:hover{
      text-decoration: underline;
    }
  }
  &__skills{
    width: 50%;
    &-item{
      display: flex;
      align-items: center;
      position: relative;
      justify-content: flex-end;
      background-color: $light-grey;
      height: 26px;
      padding-right: 11px;
      &:not(:last-child){
        margin-bottom: 15px;
      }
    }
    &-percent{
      font-size: 13px;
      font-weight: 500;
      z-index: 1;
    }
  }
  
  &__bar{
    background-color: $dark-sea;
    position: absolute;
    top: 0;
    left: 0;
    // width: 100%;
    height: 100%;
    &-name{
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100px;
      background-color: $sea;
      color: white;
      font-weight: 700;
      font-size: 14px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}