.social{
  position: relative;
  padding: 80px 0;
  background-color: $grey;
  &__up{
    position: absolute;
    top: -29px;
    left: 50%;
    transform: translateX(-50%);
    font-size: 29px;
    padding: -180px;
    padding: 12px 19px;
    display: block;
    background-color: $dark-sea;
    color: white;
    cursor: pointer;
    transition: all 100ms ease-in-out;
    &:hover{
      background-color: #126ea3;
    }
  }
  &__list{
    display: flex;
    width: fit-content;
    margin: 0 auto;
    &:hover{
      .social__item{
        opacity: 0.4;
      }
    }
  }
  &__item{
    overflow: hidden;
    width: 67px;
    height: 67px;
    transition: all 100ms ease-in-out;
    &:not(:last-child){
      margin-right: 20px;
    }
    &:hover{
      opacity: 1 !important;
    }
  }
  &__icon{
    width: 100%;
  }
}