.area-title{
  &__maintitle{
    text-transform: uppercase;
    font-size: 40px;
    width: fit-content;
    margin: 0 auto;
    position: relative;
    color: $dark;
    font-weight: 700;
    margin-bottom: 90px;
    &:after{
      content: '';
      width: calc(100% - 50px);
      background-color: #202021;
      height: 3px;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      bottom: -15px;
    }
  }
}