.projects{
  background-color: $light-grey;
  padding: 100px 0 140px;
  &__list{
    display: flex;
    flex-wrap: wrap;
  }
  &__item{
    position: relative;
    &:hover{
      .projects__info{
        opacity: 1;
        visibility: visible;
      }
      .projects__name{
        transform: translateY(0);
        opacity: 1;
      }
      .projects__bt{
        transform: translateY(0);
        opacity: 1;
        
      }
    }
  }
  &__info{
    position: absolute;
    top: 0%;
    left: 0%;
    opacity: 0;
    visibility: hidden;
    transition: all 400ms ease-in-out ;
    overflow: hidden;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: $light-grey;
  }
  &__name{
    transform: translateY(-149px);
    transition: transform 200ms ease-in-out, opacity 250ms ease-in;
    font-size: 16px;
    font-weight: 700;
    margin-bottom: 30px;
    opacity: 0;
    color: white;
  }
  &__bt{
    text-transform: uppercase;
    padding: 8px 15px;
    border: 3px solid $sea;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    transform: translateY(149px);
    transition: transform 200ms ease-in-out, opacity 250ms ease-in;
    opacity: 0;
    color: white;
    &:hover{
      background-color: $sea;
    }
  }

}