:focus{
  outline: none;
}

body {
  overflow-x: hidden;
  position: relative;
}

body, input {
  font-family: 'Red Hat Text', sans-serif;
  overflow-x: hidden;
  color: $dark;
  font-size: 12px;
  font-weight: 400;
}

.center {
  margin: 0 auto;
  position: relative;
  width: $container-desktop;
  
  @media screen and (max-width: $mobile) {
    width: 100%;
  }
}

.mobile-only {
  @media #{$mediadesktop}{
    display: none !important;
  }
}

.desktop-only {
  @media #{$mediamobile}{
    display: none !important;
  }
}

