.menu{
  background-color: #1b242f;
  border-bottom: 7px solid $sea;
  &__list{
    display: flex;
    height: 55px;
    align-items: center;
    &:hover{
      .menu__item{
        opacity: 0.8;
      }
    }
  }
  &__item{
    color: white;
    text-transform: uppercase;
    font-size: 15px;
    transition: all 190ms ease-out;
    cursor: pointer;
    &:not(:last-child){
      margin-right: 40px;
    }
    &:hover{
      opacity: 1 !important;
      color: $sea;
    }
  }
}